// =======================================
// 김효진 CSS Style 관리
// =======================================

/* 회원서비스 > 법인.단체 > 지원접수현황 입력
html/support-application-status-write.html */
.support-application-status-write-confirm-notice {
    display: inline-block;
    text-align: center;
    font-size: 20px;
    vertical-align: middle;
    letter-spacing: -0.09em;
    position: relative;
}
.support-application-status-write-btn {
    width: 200px;
    margin: 0 auto;
}

/* 회원서비스 > 회원가입안내 > 약관동의 조회
html/accept-terms-list.html */
.accept-terms-info {
    width: 930px;
    margin: 0 auto;

    .accept-terms-info-title {
        border-left: 3px solid #003761;
        font-size: 16px;
        font-weight: bold;
        padding-left: 7px;
        line-height: 16px;
        margin-bottom: 9px;
    }
    textarea {
        width: 930px;
        height: 141px;
        font-size: 13px;
        letter-spacing: -0.08em;
        overflow-y: scroll;
        border: solid 1px #d2d4d6;
        padding: 9px 10px;
    }
    .accept-terms-info-textarea1 {
        line-height: 1.38;
    }
    .accept-terms-info-textarea2 {
        line-height: 1.61;
    }
    .accept-terms-info-textarea3 {
        line-height: 1.8;
    }
    .checkbox-type {
        margin-bottom: 28px;
    }
}
.accept-terms-btn {
    width: 406px;
    margin: 0 auto;
    margin-bottom: 120px;
}

/* 회원서비스 > 회원가입안내 > 가입탈퇴 조회
html/unsubscribe-list.html */
.unsubscribe-list-content {
    width: 930px;
    margin: 0 auto;

    .unsubscribe-list-title {
        border-left: 3px solid #003761;
        font-size: 16px;
        font-weight: bold;
        padding-left: 7px;
        line-height: 16px;
        margin-bottom: 18px;
    }
}
.unsubscribe-list-form {
    position: relative;
    height: 178px;
    border-radius: 7px;
    border: 1px solid #afc0cc;
    box-shadow: 0 0 6px 6px #fdfdfd;
    background: #fff;
}
.unsubscribe-list-content .form-group {
    width: 406px;
    height: 76px;
    margin: 50px auto;

    .input-area {
        label {
            float: left;
            line-height: 34px;
            font-weight: bold;
            letter-spacing: -0.04em;
            color: #2e3a42;
            margin-right: 34px;
        }
        float: left;

        input {
            float: right;
            margin-bottom: 8px;
            clear: both;
            cursor: auto;
            width: 220px;
            height: 34px;
            line-height: 34px;
            padding: 0 10px;
            border: 1px solid #e6e9eb;
            background: #fff;
        }
    }
}
.unsubscribe-list-check {
    width: 300px;
    margin: 30px auto;

    img {
        width: 40px;
        margin-right: 20px;
        vertical-align: middle;
    }
}
.unsubscribe-list-confirm {
    display: inline-block;
    font-size: 20px;
    letter-spacing: -0.1em;
    vertical-align: middle;
}
.unsubscribe-list-content .btn-search {
    float: right;
    letter-spacing: -0.01em;
    background: #003761;
    border-bottom: 3px solid #002644;
    display: inline-block;
    min-width: 76px;
    height: 76px;
    line-height: 74px;
    text-align: center;
    padding: 0 10px;
    border-radius: 3px;
    color: #fff;
    text-shadow: 0 0 1px #003359;
    border-bottom: 3px solid #002644;
    background: #003761;
}
.unsubscribe-list-btn {
    width: 200px;
    margin: 50px auto;
}